import { Component, EventEmitter, Input, Output } from '@angular/core';
import { FormControl } from '@angular/forms';
import { Payee } from '@backbase/accessgroup-common-ang';

@Component({
  selector: 'bb-payee-group-table-row',
  templateUrl: './payee-group-table-row.component.html',
})
export class PayeeGroupTableRowComponent {
  /**
   * Payee
   */
  @Input() payee!: Payee;

  /**
   * Checkbox enabled flag
   */
  @Input() checkboxEnabled = false;

  /**
   * Checkbox form control
   */
  @Input() checkboxFormControl: FormControl = new FormControl();

  /**
   * Row view modifier
   */
  @Input() modifier: 'highlight' | 'line-thru' | 'none' = 'none';

  /**
   * Checkbox change event emitter
   */
  @Output() checkboxChange: EventEmitter<any> = new EventEmitter<any>();
}
